import React  from "react";
// react component for creating beautiful carousel
import Carousel from "react-slick";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";


// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Card from "components/Card/Card.jsx";
import carouselStyle from "assets/jss/material-kit-react/views/componentsSections/carouselStyle.jsx";
import Dots from "components/styles/dots.jsx"

// TEXT COLOR
import Info from "components/Typography/Info.jsx";

// IMAGES
// import image1 from "assets/img/bg.jpg";
// import image2 from "assets/img/bg2.jpg";
import mob from "assets/img/services/mob.svg";
import devices from "assets/img/services/device.svg";
import dbms from "assets/img/services/database.svg";
import spa from "assets/img/services/spa.svg";

class SectionCarousel extends React.Component {

  state = {
    techs:[
      { id:1, 
        techImage:mob, 
        techTitle:'Mobile App Design and Development', 
        techDisc: 'We make Android and Iphone Apps. Elegant designs, Reliable, Secure, Performance and Effecience is maintained.'
      },
      {
        id: 2,
        techImage: devices,
        techTitle: 'Responsive Web Application',
        techDisc: ''

      },
      {
        id:3,
        techImage:dbms,
        techTitle:'Database Management System',
        techDisc: ''
      },
      {
        id:4,
        techImage:spa,
        techTitle:'Single Page Apllication and Progressive Web App',
        techDisc:''
      }
    ]
  }
  render() {
    const { classes } = this.props;
    const settings = {
      dots: false,
      infinite: true,
      speed: 700,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      easing:'ease-in',
      fade: true
    };
    return (
      <div className={classes.section}>
        <Info>
          <h2  style={{textAlign:'center', marginTop:'-20px'}}>
            What we do
          </h2>
        </Info>
        <Dots />
        <div className={classes.container}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={12} className={classes.marginAuto} >
              <Card carousel style={{backgroundColor:'none', boxShadow:'none'}}>
                <Carousel {...settings} >
                  {
                    this.state.techs.map(tech =>{
                      return tech.techTitle !== '' ? (
                        <div key={tech.id}>
                          <GridContainer className={classes.marginAuto} style={{backgroundColor:'none'}}>
                            <GridItem xs={12} sm={12} md={4} >
                            <img
                              src={tech.techImage}
                              alt="Third slide"
                              className="slick-image"
                              style={{height:'150px'}}
                            />
                              </GridItem>
                              <GridItem xs={12} sm={12} md={8} >
                                <Info>
                                <h3 style={{textAlign:'center'}}>{tech.techTitle}</h3>
                                </Info>
                                <p style={{fontSize:'17px', textAlign:'center'}}>
                                  {tech.techDisc}
                                </p>
                              </GridItem>
                            </GridContainer>
                          </div>
                      ): null
                    })
                  }
                </Carousel>
              </Card>
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withStyles(carouselStyle)(SectionCarousel);
