import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Button from "components/CustomButtons/Button.jsx";
import Card from "components/Card/Card.jsx";
import CardFooter from "components/Card/CardFooter.jsx";
import Dots from "components/styles/dots.jsx"
import Tech from "components/styles/TechCard.jsx"

// TEXT COLORS
import Info from "components/Typography/Info.jsx";

import teamStyle from "assets/jss/material-kit-react/views/landingPageSections/teamStyle.jsx";

import team2 from "assets/img/faces/christian.jpg";

class TeamSection extends React.Component {
  state = {
    Members :[
      {
        id: 1,
        image: team2,
        name: 'Effort Stevens',
        title: 'Frontend Dev',
        twitter: '',
        instagram: '',
        facebook: ''
      }
    ]
  }
  render() {
    const { classes } = this.props;
    const imageClasses = classNames(
      classes.imgRaised,
      classes.imgRoundedCircle,
      classes.imgFluid
    );
    return (
      <div >
        <div className={classes.section}>
        <Info>
        <h2>Here is our team</h2>
        </Info>
        <Dots />
        <div>
          <GridContainer justify='center'>
            {
              this.state.Members.map(member =>{
                return member.title !== '' ? (
                  <GridItem xs={6} sm={6} md={3} key={member.id}>
                    <Card plain>
                      <GridItem xs={12} sm={12} md={6} className={classes.itemGrid}>
                        <img src={member.image} alt="..." className={imageClasses} />
                      </GridItem>
                      <Info>
                      <h4 >
                        {member.name}
                        <br />
                        <small className={classes.smallTitle}>
                          {member.title}
                        </small>
                      </h4>
                      </Info>
                      <CardFooter className={classes.justifyCenter}>
                        <Button
                          justIcon
                          color="transparent"
                          className={classes.margin5}
                        >
                          <i className={classes.socials + " fab fa-twitter"} style={{color:'#03c5fa'}} />
                        </Button>
                        <Button
                          justIcon
                          color="transparent"
                          className={classes.margin5}
                        >
                          <i className={classes.socials + " fab fa-instagram"}  style={{color:'#fa0376'}}/>
                        </Button>
                        <Button
                          justIcon
                          color="transparent"
                          className={classes.margin5}
                        >
                          <i className={classes.socials + " fab fa-facebook"} style={{color:'#0345fa'}}/>
                        </Button>
                      </CardFooter>
                    </Card>
                  </GridItem>
                ) : null
              })
            }
            </GridContainer>
        </div>
        </div>

        <div className={classes.section}>
          <Info>
            <h2>Technologies we use</h2>
          </Info>
          <Dots />
          <Tech />
        </div>
      </div>
    );
  }
}

export default withStyles(teamStyle)(TeamSection);
